<script>
import { mapState } from "vuex";
import constants from "../../util/constants";

export default {
  name: "sidebar",
  data() {
    return {
      constants,
      collapses: [{ show: false }, { show: false }, { show: false }],
      type_agent : "",
      service_id : -1
    };
  },
  methods: {
    updateState() {
      if (!this.est_admin) {
        this.$router.replace("/login");
      }
      // let utilisateur = JSON.parse(sessionStorage.getItem("utilisateur"));
      // this.nom_utilisateur = utilisateur.prenom + " " + utilisateur.nom;
      // this.id_utilisateur = utilisateur.id_utilisateur;
    },
    collapseAll() {
      var exp_elm = document.getElementsByClassName("show");
      if (exp_elm.length > 0) {
        var elm_id = exp_elm[0].id;
        this.$root.$emit("bv::toggle::collapse", elm_id);
      }
    },
    subIsActive(input) {
      const paths = Array.isArray(input) ? input : [input];
      return paths.some((path) => {
        return this.$route.path.indexOf(path) === 0;
      });
    },
  },
  computed: {
    ...mapState([
      "is_connected",
      "nom_utilisateur",
      "id_utilisateur",
      "est_admin",
    ]),
  },
  created() {
    let utilisateur = JSON.parse(sessionStorage.getItem("utilisateur"))
    this.type_agent = utilisateur.type
    this.service_id = utilisateur.service_id

    this.updateState();
  },
  mounted() {
    const body = document.querySelector("body");
    // add class 'hover-open' to sidebar navitem while hover in sidebar-icon-only menu
    document.querySelectorAll(".sidebar .nav-item").forEach(function (el) {
      el.addEventListener("mouseover", function () {
        if (body.classList.contains("sidebar-icon-only")) {
          el.classList.add("hover-open");
        }
      });
      el.addEventListener("mouseout", function () {
        if (body.classList.contains("sidebar-icon-only")) {
          el.classList.remove("hover-open");
        }
      });
    });
  },
  watch: {
    $route() {
      document.querySelector("#sidebar").classList.toggle("active");
      this.updateState();
    },
    sessionStorage() {
      this.updateState();
    },
  },
};
</script>

