import Vuex from "vuex";
import Vue from "vue";

Vue.use(Vuex);

let store = new Vuex.Store({
  state: {
    is_connected: sessionStorage.getItem("is_connected") ? true : false,
    nom_utilisateur: sessionStorage.getItem("nom_utilisateur")
      ? sessionStorage.getItem("nom_utilisateur")
      : "",
    id_utilisateur: sessionStorage.getItem("id_utilisateur")
      ? sessionStorage.getItem("id_utilisateur")
      : "",
    est_admin: sessionStorage.getItem("est_admin") ? true : false,
    paniers: sessionStorage.getItem("panier")
      ? JSON.parse(sessionStorage.getItem("panier"))
      : [],
  },
  mutations: {
    change_connected(state, new_value) {
      console.log(new_value);
      sessionStorage.clear();
      state.is_connected = new_value.is_connected;
      state.nom_utilisateur = new_value.prenom + " " + new_value.nom;
      state.id_utilisateur = new_value.id_utilisateur;
      state.est_admin = new_value.est_admin == "1" ? true : false;

      if (state.is_connected) {
        sessionStorage.setItem("utilisateur", JSON.stringify(new_value));
        sessionStorage.setItem(
          "nom_utilisateur",
          new_value.prenom + " " + new_value.nom
        );
        sessionStorage.setItem("is_connected", true);
        sessionStorage.setItem("id_utilisateur", new_value.id_utilisateur);
        if (new_value.est_admin == "1") {
          sessionStorage.setItem("est_admin", true);
        }
      }
    },
    add_to_panier(state, article) {
      if (state.paniers.find((pan) => pan.id_article == article.id_article)) {
        return;
      }
      state.paniers = [...state.paniers, { ...article }];
      sessionStorage.setItem("panier", JSON.stringify(state.paniers));
    },
    remove_from_panier(state, id_article) {
      state.paniers = state.paniers.filter(
        (pan) => pan.id_article != id_article
      );
      sessionStorage.setItem("panier", JSON.stringify(state.paniers));
    },
    empty_panier(state) {
      state.paniers = [];
      sessionStorage.setItem("panier", JSON.stringify(state.paniers));
    },
  },
});

export default store;
